<template>
  <div class="param-container">
    <div class="param-item">
      <GearsIcon :color=color></GearsIcon>
      {{el}}
    </div>
    <div class="param-item">
      <ArrowIcon :color=color></ArrowIcon>
      {{size}}
    </div>
    <div class="param-item">
      <WeightIcon :color=color></WeightIcon>
      {{weight}}
    </div>
  </div>
</template>

<script>
  import GearsIcon from './svg/gears';
  import ArrowIcon from './svg/arrows';
  import WeightIcon from './svg/weight';

  export default {
    name: 'params',
    components: {
      GearsIcon, ArrowIcon, WeightIcon
    },
    props: [
      'color',
      'el',
      'size',
      'weight'
    ]


  }
</script>
<style>
  .svg-param-icon {
    height: 45px;
  }
  .param-container{
    display: flex;
    padding: 30px;

  }
  .param-item{
    display: flex;
    align-items: center;
    padding: 0 40px;
  }


  @media (max-width: 480px) {
  .param-container .param-item {
    padding: 0 20px;
  }}
</style>



