<template lang="pug">

  main
    include pug/footer.pug
    include pug/header.pug
    include pug/history.pug
    include pug/polit.pug
    +history()
    +polit()
    .form(v-if='forms')
      form.form-content(v-on:submit.prevent='zakaz()')
        .close(@click='forms=false; zakazp=false') x
        .group
          input(type="text" required v-model="name")
          span.highlight
          span.bar
          label Имя
        .group
          input(type="tel" required v-model="tel" number)
          span.highlight
          span.bar
          label Телефон
        .group(v-if='currentmodel<999')
          input(type="text" required v-model="city")
          span.highlight
          span.bar
          label Город
        .group(v-if='currentmodel<999')
          input(type="email" required v-model="email")
          span.highlight
          span.bar
          label Email
        .group
          button(type="submit" ) Заказать


    //--------------------------------------------------------
    .zayav(v-if='forms==false && spasibo==true')
      +header()
      .prin.spasibo
        h2 Заявка принята!
        span.prin-text-invert Скоро мы с вами свяжемся
        a.zayav-home(@click='home()') На главную
    .zayav(v-if='currentmodel>-1 && currentmodel<999 && zakazp==true')
      +header()

      .prin(v-if='spasibo==false')
        h2 Заявка принята!
        span.prin-text-invert Скоро мы с вами свяжемся
        span.prin-text При выборе одной и более опций доставка
          span.prin-text-b  БЕСПЛАТНО!
      .zayav-main(v-if='spasibo==false')
        h4 Улучшите выбранную модель:
        .zayav-item(v-if="currentmodel==1 || currentmodel==0")
          .zayav-item-img.zayav-item-img1
          .zayav-item-r
            .zayav-item-text Крутая светодиодная опция без проводов, 7 цветов, работает при включении вилки
            .zayav-item-textb Сделать бизиборд максимально ярким и красочным?
            .zayav-item-cen 1900 рублей
            .zayav-item-btns
              .btn-ok(v-if='!dop[1].check' @click='add(1)')
              .btn-no(v-if='dop[1].check' @click='minus(1)')
        .zayav-item(v-if="currentmodel==0 || currentmodel==1 ")
          .zayav-item-img.zayav-item-img2
          .zayav-item-r
            .zayav-item-text  Доска для рисования, на обратной стороне

            .zayav-item-textb Сделать бизиборд максимально устойчивым,  безопасным и занимательным?
            .zayav-item-cen 1250 рублей
            .zayav-item-btns
              .btn-ok(v-if='!dop[2].check' @click='add(2)')
              .btn-no(v-if='dop[2].check' @click='minus(2)')
        .zayav-item(v-if="currentmodel==2 ")
          .zayav-item-img.zayav-item-img7
          .zayav-item-r
            .zayav-item-text  Доска для рисования, на обратной стороне

            .zayav-item-textb Сделать бизиборд максимально устойчивым,  безопасным и занимательным?
            .zayav-item-cen 1700 рублей
            .zayav-item-btns
              .btn-ok(v-if='!dop[7].check' @click='add(7)')
              .btn-no(v-if='dop[7].check' @click='minus(7)')
        .zayav-item
          .zayav-item-img.zayav-item-img3
          .zayav-item-r
            .zayav-item-text Гравировка имени ребенка на дверке

            .zayav-item-textb Сделать бизиборд максимально красивым?
            .zayav-item-cen 999 рублей
            .zayav-item-btns
              .btn-ok(v-if='!dop[3].check' @click='add(3)')
              .btn-no(v-if='dop[3].check' @click='minus(3)')
        .zayav-item(v-if="currentmodel==999")
          .zayav-item-img.zayav-item-img4
          .zayav-item-r
            .zayav-item-text
            .zayav-item-textb
            .zayav-item-cen
            .zayav-item-btns
              .btn-ok
              .btn-no
        .zayav-item
          .zayav-item-img.zayav-item-img5
          .zayav-item-r
            .zayav-item-text Дисковый набиратель номера

            .zayav-item-textb Сделать бизиборд максимально занятным?
            .zayav-item-cen 999 рублей
            .zayav-item-btns
              .btn-ok(v-if='!dop[5].check' @click='add(5)')
              .btn-no(v-if='dop[5].check' @click='minus(5)')
        .zayav-itog(v-if='sum>0')
          .zayav-itog-text Итого на сумму:
          .zayav-itog-cen {{sum}} руб.
        .zayav-btns
          .btn.zayav-ok.btn-color8(v-on:click='sendMaildop()') Включиь в заказ
          a.zayav-home(@click='zakazp=false') На главную
    .main(v-if='zakazp==false  && spasibo==false ')
      section.block.block-big.block1
        .container
          .block1_logo
            .logo
              img(src="../assets/img/logo.png")
          .block1_content
            .block1-left(v-if='v1')
              h1.h1.h11 Бизиборды
                br
                span в современном стиле
              .block1-left-dost
                .dost Доставим по Москве в течении
                  br
                  | 24 часов, в регионы от 3х дней
                .gift Подарки, акции, интересные
                  br
                  | дополнения к доске
              .min-price от 3990 рублей
              .dop_int
                span.red-text +
                | Интересные дополнения
                br
                | к каждой модели!


            .block1-left(v-if='!v1')
              h1.h1.h1v2 Развивающая игрушка
                br
                span.spanh11 для детей от 6 мес до 3х лет
                br
                span.spanh12 ЭФФЕКТ УЖЕ В ПЕРВУЮ НЕДЕЛЮ!
              .block1-left-dost
                .clock Освобождает маме время
                  br
                  | каждый день!
                .hand Развивает моторику рук

                  br
                  | ребенка, усидчивость и логику!
                .girl Учит одеваться и правильно
                  br
                  | использовать бытовые предметы!


            .block1-right
              .block1-circle(v-if='v1') 6100
                br
                span рублей

      section.block.block-big.block12(v-if='!v1' v-bind:style="{ 'background-color': '#fff!important' }")
        .container
          h2 Что такое бизиборд или,
            br
            |  в переводе, занятная доска
          .block12-photorow
            .block12-image
              img(src="../assets/img/photo.png")
            .block12-photorow-text Игрушка созданная по принципам системы воспитания Марии Монтессори (итальянский педагог XIX века), которая доказала, что САМОЕ ЭФФЕКТИВНОЕ время для развития, в жизни человека, период с 6 мес до 6,5 лет и разработала уникальную методику развития детей в этом возрасте.
          .block12-textrow
            | *Бизиборд рекомендуется покупать детям в возрасте от 6 мес до 3х лет включительно.
            br
            | Детям старше 4х лет (без отклонений в развитии) он будет полезен и интересен, но в меньшей степени.
      section.block.block-big.block2
        .container
          h2 Чему научится  Ваш ребенок,
            h3 быстрее своих сверстников, в процессе
              br
