          .learn
            .learn-block
              .learn-item
                .learn-item-icon.learn-item-icon1
                .learn-item-text
                  | Точная координация рук
                  br
                  | и крепкий захват предметов
                  br
                  | в руках, например - ложки.
              .learn-item
                .learn-item-icon.learn-item-icon2
                .learn-item-text
                  | Открывать замочки
                  br
                  | и открыться если случайно закроется
            .learn-block
              .learn-item
                .learn-item-icon.learn-item-icon3
                .learn-item-text
                  | Поймет предназначение розетки и не будет пихать в неё
                  br
                  | опасные предметы
              .learn-item
                .learn-item-icon.learn-item-icon4
                .learn-item-text
                  | Завязывать
                  | шнурки
          img.learn-image(src="../assets/img/learn.png")
      section.block.block-big.block21
        .container
          h2 Как создаётся бизиборд?
          iframe(src="https://www.youtube.com/embed/6gETRWm3LdU" frameborder="0" allowfullscreen).how-video
          h3.how-h3 У нас сертифицированное производство.
