              .block3_list-text Бизиборд, занятная доска -
              .block3_list-text-b ОСВОБОЖДАЕТ ВАШЕ ВРЕМЯ
            li
              GearIcon
              .block3_list-text Ваш малыш будет осваивает элементы бизиборда - постепенно
              .block3_list-text-b ИГРУШКА НА ДОЛГОЕ ВРЕМЯ!
            li
              GearIcon
              .block3_list-text
                | Детали наших бизибордов подбирались по принципу М.Монтессори.
                br
                | Во время игры, Ваш ребенок овладеет базовыми навыками
              .block3_list-text-b ЭФФЕКТ УЖЕ В ПЕРВУЮ НЕДЕЛЮ!
            li
              GearIcon
              .block3_list-text Современный дизайн наших бизибордов, органично впишется в Ваш интерьер
              .block3_list-text-b КРАСОТА В ВАШЕМ ДОМЕ!
      section.block.block-small.block4
        .container
          .eco-icon
          .eco-text ЭКОЛОГИЧНОСТЬ МАТЕРИАЛОВ 100%
          .eco-text-invert
