              .table-image-item.table-image-item2(v-bind:class="{none:house_select!=2 }")
              .table-image-item.table-image-item3(v-bind:class="{none:house_select!=3 }")



              a.btn.btn-max.btn-color6(v-on:click="createform(8,1)" v-if="house_select!=3") Заказать за 2250 руб.
                br
