<script>
  import GearIcon from './svg/gear';
  import Params from './params';
  import Forms from './form';
  import axios from 'axios';
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons'
  import {yandexMap, ymapMarker} from 'vue-yandex-maps'


  export default {
    name: 'hello',
    components: {
      GearIcon,
      Params,
      Forms,
      Icon,
      yandexMap,
      ymapMarker,


    },
    data() {
      return {
        modalclose: false,
        keyinput: true,
        mousescroll: false,
        showclosebutton: true,
        showcaption: false,
        imagecountseparator: 'of',
        showimagecount: true,
        showthumbnails: false,

        history: false,
        polit: false,
        index: null,
        spasibo: false,
        forms: false,
        house_select: 1,
        bstprc_select: 1,
        maxi_select: 1,
        case_select: 1,
        case2_select: 1,
        case3_select: 1,
        double_select: 1,
        color_select: 1,
        sw_select: 1,
        selected: '',
        colors: [[
          'Бело-зелёный',
          'Коричнево-зелёный',
          'Сине-желтый',
          'Красно-белый',
          'Розово-мятный',
          'Желто-зелёный',
          'Бело-красный',
          'Бело-фиолетовый',
          'Розово- белый',
        ], [
          'Коричнево-зелёный',
          'Бело-рыжий',
          'Сине-рыжий',
          'Сине-белый',
          'Розово-мятный',
          'Розово-белый'
        ], [
          'Мятно-розовый',
          'Розово-мятный',
          'Бело-красный'
        ], [''], [''], ['']],
        models: [
          'Модель BEST-PRICE',
          'Модель MAXI',
          'Модель CASE',
          'Модель DOUBLE 2 в 1',
          'Светодиодная модель COLOR',
          'Самая роскошная модель swarovski',
          '',
          'Модель HOUSE'
        ],
        sendemail: '',
        name: '',
        tel: '',
        email: '',
        city: '',
        currentmodel: 0,
        zakazp: false,
        sum: 0,

        dop: [{
          name: '',
          cen: 0,
          check: false
        }, {
          name: 'Светодиодная опция',
          cen: 1900,
          check: false
        }, {
          name: 'Доска для рисования',
          cen: 1250,
          check: false
        }, {
          name: 'Гравировка',
          cen: 999,
          check: false
        }, {
          name: '',
          cen: 0,
          check: false
        }, {
          nname: 'Дисковый набор',
          cen: 999,
          check: false
        }, {
          name: 'Доска для рисования',
          cen: 1700,
          check: false
        }, {
          name: 'Доска для рисования',
          cen: 1700,
          check: false
        },
        ],
        v1: true

      }
    },
    methods: {
      home() {
        this.zakazp = false;
        this.spasibo = false;
      },
      createform: function (model, color) {
        this.currentmodel = model - 1

        this.forms = true
        if (this.currentmodel != 999)
          this.selected = this.models[model - 1] + ' ' + this.colors[model - 1][color - 1]
        else this.selected = 'Заказ телефонного звонка'

      },
      zakaz() {
        this.forms = false
        this.spasibo = true;
        if (this.currentmodel < 4) {
          this.zakazp = true
          this.spasibo = false;
        }

        scroll(0, 0)
        this.sendMail()

        //console.log(this.currentmodel, this.zakazp)

      },
      add(number) {
        this.dop[number].check = true;
        this.sum = this.sum + this.dop[number].cen
      },
      minus(number) {
        this.dop[number].check = false;
        this.sum = this.sum - this.dop[number].cen
      },
      sendMail() {
        let title = "Заказ"
        let msg = 'Имя: ' + this.name + '\n ' + 'Телефон: ' + this.tel + '\n '
        if (this.city.length > 1) {
          msg = msg + 'Город: ' + this.city + '\n ' + 'Email: ' + this.email + ' ' + '\n' + 'Заказ: '
        }
        msg = msg + this.selected
        var params = new URLSearchParams();
        params.append('title', title)
        params.append('msg', msg);
        axios.post('mail.php', params)
        //this.spasibo=true
      },
      sendMaildop() {
        let title = 'Дополнения к заказу'
        let msg = 'Email: ' + this.email + ' ' + '\n'
        for (let i of this.dop) {
          //console.log(i)
          if (i.check) {
            msg = msg + i.name + '\n'
          }
          //console.log(msg)
        }
        var params = new URLSearchParams();
        params.append('title', title)
        params.append('msg', msg);
        axios.post('mail.php', params)
        this.currentmodel = 0;
        this.zakazp = false
        this.spasibo = true
      }

    }
  }


</script>

