  .btn {
    cursor: pointer;
    z-index: 10;
    text-decoration: none;
    outline: none;
    display: inline-block;
    padding: 15px 25px;
    margin: 10px 20px;
    border-radius: 10px;
    font-size: 18px;
    font-weight: 700;
    line-height: 21px;
    text-align: center;
    transition: .15s ease-in-out;
    font-family: HelveticaNeueCyr;
    &:hover {
      background: $color_4;
    }
    &-color8 {
      box-shadow: 0 0 40px 40px $color_8 inset, 0 0 0 0 $color_8;
      color: $color_4;
      &:hover {

        box-shadow: 0 0 10px 0 $color_8 inset, 0 0 10px 4px $color_8;
        color: $color_8;
        cursor: hand;
      }
    }
    &-color6 {
      box-shadow: 0 0 40px 40px $color_6 inset, 0 0 0 0 $color_6;
      color: $color_4;
      &:hover {
        box-shadow: 0 0 10px 0 $color_6 inset, 0 0 10px 4px $color_6;
        color: $color_6;
        cursor: hand;
      }
    }
    &-color3 {
      box-shadow: 0 0 40px 40px $color_3 inset, 0 0 0 0 $color_3;
      color: $color_4;
      &:hover {
