  @mixin table-menu-item_hover($color) {

    .table-menu-item:hover {
      background: $color;
      font-weight: 700;
      &:after {
        content: '';
        position: absolute;
        left: 100%;
        bottom: 10px;
        border: 7.5px solid transparent;
        border-left: 8px solid $color;
        display: block;
        width: 0;
        height: 0;
      }
    }

  }

