  .form {

    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    position: fixed;
    z-index: 100;
    min-height: 100vh;
    min-width: 100vw;
    background: rgba(20, 20, 20, .5);
    .close {
      position: absolute;
      right: 20px;
      top: 20px;
      cursor: pointer;
      &:hover {
        text-shadow: 0.1em 0.1em 0.2em black;
      }
    }

  }

  .form-content {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 300px;
    height: 400px;
    background: white;
    border-radius: 10px;
    box-shadow: 0px 13px 20px 0px rgba(25, 21, 50, 0.4);
  }

  .group {
    position: relative;
    margin: 12px;

    input {
      font-size: 18px;
      padding: 10px 10px 10px 5px;
      display: block;
      width: 90%;
      border: none;
      border-bottom: 1px solid #757575;
    }

    input:focus {
      outline: none;
    }

    label {
      color: #999;
      font-size: 18px;
      font-weight: normal;
      position: absolute;
      pointer-events: none;
      left: 5px;
      top: 10px;
      transition: 0.2s ease all;
      -moz-transition: 0.2s ease all;
      -webkit-transition: 0.2s ease all;
    }

    input:focus ~ label, input:valid ~ label {
      top: -20px;
      font-size: 14px;
      color: #5264AE;
    }

    button {
      display: block;

      border: none;
      font-size: 18px;
      border-radius: 5px;
      padding: 5px 10px;
      &:hover {
        box-shadow: 0px 13px 20px 0px rgba(25, 21, 50, 0.4);
        color: white;
        background: #5264AE;
      }
    }
  }

