    .block1-left .h1v2 {
      font-size: 21px !important;
    }
    .block1-left .h1v2 span {
      font-size: 18px !important;
    }
    .block1-left .h1 .spanh12 {
      font-size: 16px !important;
    }
    .block12-photorow-text {
      min-width: 300px;

    }
    footer {
      .container {
        flex-direction: column;
      }
      .footer-left-bottom{
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        a,span{
          margin: 5px;
        }
      }
      .footer-right-bottom{
        justify-content: center;
        align-items: center;
      }
    }

  }

  .lightbox__image img {
    height: 100vh;
  }
</style>



